import React, { useState } from 'react'
import { Typography, Paper, Avatar, Button, FormControl, Input, InputLabel } from '@material-ui/core'
import LockOutlinedIcon from '@material-ui/icons/LockOutlined'
import withStyles from '@material-ui/core/styles/withStyles'
import { Link, withRouter } from 'react-router-dom'
import firebase from '../firebase'
import '../App/index.css';

const styles = theme => ({
	main: {
		width: 'auto',
		display: 'block', // Fix IE 11 issue.
		marginLeft: theme.spacing.unit * 3,
		marginRight: theme.spacing.unit * 3,
		[theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
			width: 400,
			marginLeft: 'auto',
			marginRight: 'auto',
		},
	},
	avatar: {
		margin: 20
	  },
	  bigAvatar: {
		margin: 10,
		width: 120,
		height: 120
	  },
	paper: {
		marginTop: theme.spacing.unit * 6,
		display: 'flex',
		flexDirection: 'column',
		alignItems: 'center',
		background:
      "linear-gradient(89deg, rgb(209, 212, 30) 0%, rgb(47, 206, 47) 100%)",
		padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
	},
	avatar: {
		margin: theme.spacing.unit,
		backgroundColor: theme.palette.secondary.main,
	},
	form: {
		width: '100%', // Fix IE 11 issue.
		marginTop: theme.spacing.unit,
	},
	paper2: {
		marginTop: theme.spacing.unit * 2,
		display: "flex",
		flexDirection: "column",
		alignItems: "center",
		background:
		  "linear-gradient(89deg, rgb(209, 212, 30) 0%, rgb(47, 206, 47) 100%)",
		padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 2}px ${theme
		  .spacing.unit * 2}px`
	  },
	submit: {
		marginTop: theme.spacing.unit * 3,
	},
});

function SignIn(props) {
	const { classes } = props

	const [email, setEmail] = useState('')
	const [password, setPassword] = useState('')

	return (
		<main className={classes.main}>
			<Paper className={classes.paper2}>
			<Typography component="default" variant="default">
			<p> NewHire.inðŸŽ“</p>
			</Typography>
			</Paper>
			<Paper className={classes.paper}>
			<Avatar
            alt="Remy Sharp"
            src="/img/newhire.PNG"
            className={classes.bigAvatar}
          />
				<Typography component="default" variant="default">
					Sign in
       			</Typography>
				<form className={classes.form} onSubmit={e => e.preventDefault() && false}>
					<FormControl margin="normal" required fullWidth>
						<InputLabel htmlFor="email">Email Address</InputLabel>
						<Input id="email" name="email" autoComplete="off" autoFocus value={email} onChange={e => setEmail(e.target.value)} />
					</FormControl>

					
					<FormControl margin="normal" required fullWidth>
						<InputLabel htmlFor="password">Password</InputLabel>
						<Input id="password" name="password" autoComplete="off" autoFocus value={password} onChange={e => setPassword(e.target.value)} />
					</FormControl>
					<Button
						type="submit"
						fullWidth
						variant="contained"
						color="default"
						onClick={login}
						className={classes.submit}>
						Sign in
          			</Button>
					<Button
						type="submit"
						fullWidth
						variant="contained"
						color="default"
						component={Link}
						to="/register"
						className={classes.submit}>
						Register
					  </Button>
					  <Button
						type="submit"
						fullWidth
						variant="contained"
						color="default"
						component={Link}
						to="/"
						className={classes.submit}>
						Home
          			</Button>
				</form>
			</Paper>
			<Paper className={classes.paper}>
			<p>
			  Copyright &copy; 2020 NEWHIRE.IN| Website Designed by{" "}
			  <a href="http://www.mindlink.in" target="_parent">
				<em>MINDLINK.IN</em>
			  </a>
			</p>
		  </Paper>
		</main>
	)

	async function login() {
		try {
			await firebase.login(email, password)
			props.history.replace('/dashboard')
		} catch(error) {
			alert(error.message)
		}
	}
}

export default withRouter(withStyles(styles)(SignIn))